import React, { useEffect, useState } from "react";
import { navigate } from "gatsby";
import { supabase } from "../../../database/supabaseClient";
import "./protected.css";

const i18n = {
  EN: {
    brandTitle: "TINH HOA VIỆT",
    tagline: "Where Vietnamese Excellence Meets Global Recognition",
    loading: "Checking authentication...",
    redirect: "Redirecting...",
  },
  VN: {
    brandTitle: "TINH HOA VIỆT",
    tagline: "Nơi tôn vinh tinh hoa Việt Nam vươn tầm quốc tế",
    loading: "Đang kiểm tra xác thực...",
    redirect: "Đang chuyển hướng...",
  },
};

interface ProtectedRouteProps {
  children: React.ReactNode;
  redirectTo?: string;
  language?: "EN" | "VN";
}

const ProtectedRoute: React.FC<ProtectedRouteProps> = ({
  children,
  redirectTo = "/members/login",
  language = "EN",
}) => {
  const [session, setSession] = useState<any>(null);
  const [status, setStatus] = useState(i18n[language].loading);
  const [loading, setLoading] = useState(true);

  const t = i18n[language];

  useEffect(() => {
    const checkSession = async () => {
      const { data, error } = await supabase.auth.getSession();

      if (error) {
        console.error("Session error:", error);
        setStatus("⚠️ Error checking session.");
        setLoading(false);
        return;
      }

      if (!data.session) {
        setStatus("❌ You must be logged in to view this page.");
        setTimeout(() => navigate(redirectTo), 1800);
      } else {
        setSession(data.session);
        setStatus("✅ Access granted!");
      }

      setLoading(false);
    };

    checkSession();

    const { data: listener } = supabase.auth.onAuthStateChange((_event, session) => {
      setSession(session);
      if (!session) {
        setStatus(t.redirect);
        setTimeout(() => navigate(redirectTo), 1500);
      }
    });

    return () => {
      listener.subscription.unsubscribe();
    };
  }, [redirectTo, t.redirect]);

  if (loading || !session) {
    return (
      <div className="auth-shell">
        <aside className="brand-side" aria-hidden>
          <div className="brand-sheen" />
          <div className="brand-pattern" />
          <div className="brand-wrap">
            <h1 className="brand-name">{t.brandTitle}</h1>
            <p className="brand-tag">{t.tagline}</p>
          </div>
        </aside>

        <main className="form-side">
          <div className="loading-block">
            <div className="spinner" />
            <p className="loading-text">{status}</p>
          </div>
        </main>
      </div>
    );
  }

  return <>{children}</>;
};

export default ProtectedRoute;
